import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Menu from './navBarComponents/Menu';
import { Container } from '@material-ui/core';
import logo from '../resources/images/logo.png'
const useStyles = makeStyles();

export default function NavBar() {
  const classes = useStyles();

  return (
    <AppBar className={classes.appBar} style={{ background: 'black' }} position="sticky">
        <Toolbar >
          <IconButton className={classes.menuButton} color="white" aria-label="menu">
            <Menu />
          </IconButton>
         <Container align="center">
           <img style={{height: 'auto', maxWidth: '10em', margin: '1em'}} src={logo} alt="Logo" /> 
          </Container>
          <Button color="inherit">Log-in/Sign-up</Button>
        </Toolbar>
      </AppBar>
  );
}