import React from 'react';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import useStyles from '../../styles/styles';
import Image from '../../resources/images/1.png'
import Container from '@material-ui/core/Container';
import { CardContent, Grid } from '@material-ui/core';


export default function GetYourMoneyCard() {
  const classes = useStyles();
  return ( 
        <Container className={classes.card} >
        <CardMedia
          className={classes.cardMedia}
          image={Image}
          title="Image title"
        />
        <CardContent  >
            <Grid 
            container
            spacing={0}
            direction="column"
            alignItems="center"
            justify="center"
            >
              <Typography gutterBottom variant="h5" component="h2" alignItems="ceneter">
              Get Your Money
              </Typography>
              <Typography>
              School Event Tickets provides deposits weekly without request.
              </Typography>
          </Grid>
        </CardContent>
        </Container>
  );
}
