import React from 'react';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import useStyles from '../../styles/styles';
import Image from '../../resources/images/1.png'
import Container from '@material-ui/core/Container';
import { CardContent, Grid } from '@material-ui/core';


export default function QRcard() {
  const classes = useStyles();
  return ( 
        <Container className={classes.card} >
        <CardMedia
          className={classes.cardMedia}
          image={Image}
          title="Image title"
        />
        <CardContent  >
            <Grid 
            container
            spacing={0}
            direction="column"
            alignItems="center"
            justify="center"
            >
              <Typography gutterBottom variant="h5" component="h2" alignItems="ceneter">
              Scan Your Tickets
              </Typography>
              <Typography>
              Quickly scan tickets from any phone or tablet using the School Event Ticket Scanner iOS or Android app. 
              </Typography>
              <Typography>
                <ul>
                   <li>
                   User-level security to app for scanning tickets
                   </li>
                   <li>
                   Tickets are scanned in real time preventing duplicate tickets from being used
                   </li>
                   <li>
                   Alerts for Valid Ticket, Duplicate Ticket or Wrong Event will appear after tickets are scanned
                   </li>
                   <li>
                   Check In/Out Ability for Increased attendee management
                   </li>
                   <li>
                   Search guest names quickly for lost tickets to allow check-in on the spot
                   </li>
                </ul>
              </Typography>
          </Grid>
        </CardContent>
        </Container>
  );
}
