
import React from 'react';
import Grid from '@material-ui/core/Grid';
import useStyles from '../styles/styles';
import SellTicketsCard from './cardComponents/SellTicketsCard';
import QRcard from './cardComponents/QRcard';
import GetYourMoneyCard from './cardComponents/GetYourMoneyCard';
import { Carousel } from 'react-bootstrap';


function Cards() {
  const classes = useStyles();

  return (
    <React.Fragment className={classes.cardContianer}>
      <Grid
        container
        direction="row"
        justify="space-evenly"
        // alignItems="center"
      >
        <Grid className={classes.cardContianer} alignItems="flex-start" item xs={6}>
          <SellTicketsCard  />
        </Grid>
        <Grid  item xs={6} alignItems="flex-end">
          <QRcard />
        </Grid>
        <Grid item xs={6} alignItems="flex-end">
          <GetYourMoneyCard />
          </Grid>
          </Grid>
    </React.Fragment>
  );
}

export default Cards;
