import React from 'react';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import useStyles from '../../styles/styles';
import Container from '@material-ui/core/Container';
import Image from '../../resources/images/2.png'
import { Card, CardContent, Grid } from '@material-ui/core';

import { makeStyles } from '@material-ui/core/styles';
import Popover from '@material-ui/core/Popover';
import Button from '@material-ui/core/Button';



export default function SellTicketsCard() {

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;
  const classes = useStyles();
  return (
    <Container className={classes.card} >
      <CardMedia
        className={classes.cardMedia}
        image={Image}
        title="Image title"
      />
      <CardContent  >
        <Grid
          container
          spacing={0}
          direction="column"
          alignItems="center"
          justify="center"
        >
          <Typography gutterBottom variant="h5" component="h2" alignItems="ceneter">
            Sell Your Tickets
          </Typography>
          <Typography>
            Consumers have 24-hour access to tickets directly from our site or app. Tickets are conveniently received by email to print at home or download to a mobile device.
          </Typography>
          <Typography gutterBottom variant="p" component="h2" alignItems="ceneter">
            <Button aria-describedby={id} variant="contained" color="black" onClick={handleClick}>
              <h4>...</h4>
            </Button>
          </Typography>
          <Popover
            id={id}
            open={open}
            anchorEl={anchorEl}
            onClose={handleClose}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'center',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'center',
            }}
          >
            <Typography className={classes.typography}>
              <Card>
                <CardContent>
                  <Grid
                    container
                    spacing={0}
                    direction="column"
                    alignItems="center"
                    justify="center"
                  >
                    <Typography>
                      <ul>
                        <li>
                        Fully personalized to the client’s website and brand
                   </li>
                        <li>
                        Customizable Event and Venue Information
                   </li>
                        <li>
                        Access codes assist with prioritizing access for parents and guardians
                   </li>
                        <li>
                        Offer Season tickets to your team's biggest fans. 
                   </li>
                        <li>
                        Share Tickets by Email or Text Message
                   </li>
                        <li>
                        Free customizable ad space to sell to your sponsors
                   </li>
                      </ul>
                    </Typography>
                  </Grid>
                </CardContent>
              </Card>
            </Typography>
          </Popover>
        </Grid>
      </CardContent>
    </Container>
  );
}
